import {  
    mapMutations  
} from 'vuex';
export default {
	data() {
		return {
			version:''
		};
	},
	onLoad(){
		plus.runtime.getProperty(plus.runtime.appid,(wgtinfo)=>{
			// this.$api.msg(wgtinfo.version);   //版本号
			this.version = wgtinfo.version
		})
	},
	methods:{
		...mapMutations(['logout']),
		navTo(url){
			uni.navigateTo({
				url:url
			})
		},
		
		/* clear(){
			uni.removeStorage({
				key: 'userInfo'
			})
		}, */
		//退出登录
		toLogout(){
			uni.showModal({
			    content: '确定要退出登录么',
			    success: (e)=>{
			    	if(e.confirm){
			    		this.logout();
						uni.navigateBack();
			    	}
			    }
			});
		},
		//switch
		switchChange(e){
			let statusTip = e.detail.value ? '打开': '关闭';
			this.$api.msg(`${statusTip}消息推送`);
		},

	}
}
